<template>
	<view class="container">
		<view>111111</view>
		<!-- <uni-swiper @change="swiperChange">
			<uni-swiper-item v-for="(day, index) in weeksDate" :key="index">
				<view style="flex-direction:column;">
					<text style="display: block;">{{day.week}}</text>
					<text>{{day.day}}</text>
				</view>
			</uni-swiper-item>
		</uni-swiper> -->
<!-- 		<scroll-view class="calendar-scroll" v-for="(day, index) in weeksDate" :key="index" scroll-x="true" style="width: 100%;white-space:nowrap;
">
		  <view class="calendar-item" style="display: inline-block; width: 100px; text-align: center;white-space:nowrap">
		    {{day.week}}
			<br>
			{{day.day}}
		  </view>
		</scroll-view> -->
	</view>
</template>


<script>
	import lunar from 'chinese-lunar';
	
	export default {
		data() {
			return {
				weeks: [],
				weeksDate:[],
			};
		},
		onShow(option) {
			// this.init();
		},
		methods: {
			swiperChange(){
				
			},
			getFormat(date, fmt) { 
				let phoneType = uni.getSystemInfoSync().platform;
				if (phoneType == 'ios') {
					date = new Date(date.replaceAll('-', '/'))
				} else {
					date = new Date(date)

				}
				let o = {
					"M+": date.getMonth() + 1, //月份
					"d+": date.getDate(), //日
					"H+": date.getHours(), //小时
					"m+": date.getMinutes(), //分
					"s+": date.getSeconds(), //秒
					"q+": Math.floor((date.getMonth() + 3) / 3), //季度
					"S": date.getMilliseconds() //毫秒
				};
				if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (date.getFullYear() + "").substr(4 - RegExp.$1.length));
				for (let k in o) {
					if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[
						k]) : (("00" + o[k]).substr(("" + o[k]).length)));
				}
				if (phoneType == 'ios') {
					fmt = fmt.replaceAll('-', '/')
				}
				return fmt;
			},
			tapDate(date) {
				this.selectday = date;
			},
			change(e) {

			},
			curBtn(index) {
				this.curIndex = index;
			},
			init(selectDay) {
				let today = new Date();
				if(selectDay !== undefined && selectDay !== null && selectDay !== ""){
					today = selectDay
				}
				let dayOfWeek = today.getDay(); // 获取今天是周几
				let delta = 4 - dayOfWeek; // 计算今天距离周四的偏移量
				let startDate = new Date(today.getTime() - delta * 24 * 60 * 60 * 1000); // 计算本周的第四天日期
				for (let i = 0; i < 7; i++) {
					const date = new Date(startDate.getTime() + i * 24 * 60 * 60 * 1000);
					this.weeks[i] = (date);
				}
				console.log(this.weeks)
				this.weeks.forEach((item,index)=>{
					//获取临时日期
					let tempDate = item
					let dayOfWeek = tempDate.getDay();//获取是周几
					let dayOfMonth = tempDate.getDate();//获取是几号
					// let lunarDay =  lunar.solarToLunar(tempDate);//获取农历日期
					this.weeksDate.push({
						week:this.convertWeeks(dayOfWeek),
						day:dayOfMonth,
						// lunar:lunarDay
					})
				})
				console.log(this.weeksDate)
			},
			convertWeeks(value){
				let that = this
				if(that.isNotEmpty(value)){
					let str = null;
					switch(value){
						case 0 :
							str = "日";
							break;
						case 1 :
							str = "一";
							break;
						case 2 :
							str = "二";
							break;
						case 3 :
							str = "三";
							break;
						case 4 :
							str = "四";
							break;
						case 5 :
							str = "五";
							break;
						case 6 :
							str = "六";
							break;	
						default :
							break;
					}
					return str
				}else{
					return null
				}
			},
			isNotEmpty(value){
				if(value!==undefined && value !== null && value !== ""){
					return true
				}else{
					return false
				}
			}
		},
	};
</script>

<style scoped>
	.container {
		height: 150rpx;
		border-bottom: 1.5px solid #e0e0e0;
	}
</style>